import Vue from 'vue'
import Router from 'vue-router'
import VendorSearch from '../components/VendorSearch.vue'
import Vendor from '../components/Vendor.vue'
import FoodItem from '../components/FoodItem.vue'
import Home from '../components/Home.vue'
import Account from '../components/Account.vue'


Vue.use(Router)

const router = new Router({
    routes: [{
            path: '/',
            name: 'home',
            component: Home,
            meta: { title: 'Food Universe | Food Universe' }
        },
        {
            path: '/SearchResults',
            name: 'SearchResults',
            component: VendorSearch,
            meta: { title: 'SearchResults | Food Universe' }
        },
        {
            path: '/vendor/:ZNavKey',
            name: 'Vendor',
            component: Vendor,
            meta: { title: 'Vendor | Food Universe' }
        },
        {
            path: '/fooditem/:ZNavKey',
            name: 'FoodItem',
            component: FoodItem,
            meta: { title: 'FoodItem | Food Universe' }
        },
        {
            path: '/account',
            name: 'Account',
            component: Account,
            meta: { title: 'My Account | Food Universe', description: 'User account settings for Food Universe' }
        }
    ]
})

router.beforeEach((to, from, next) => {
    document.title = to.meta.title
    document.description = to.meta.description
    next()
})

export default router


