<script>
// Library components.
import axios from 'axios';
import { mapState } from 'vuex'
// Custom components.
import router from '../../router'

export default {
    name: 'DietarySettings',
    props: ['selecttab'],
    components: {
    },

    data() {
        return {
          ZNavKey: 'CE89FE19-8B6504A2C-AEC5-59B40B8153C1',
          endpoint: 'https://api.fooduniverse.com/service/api/data/doquery',
          loading: false,
          dietarysettings: {
                  // "userid": 49,
                  // "desc1": "Daniel Doble",
                  // "firstname": "Daniel",
                  // "lastname": "Doble",
                  // "fullname": "Daniel Doble",
                  // "street": null,
                  // "street2": null,
                  // "city": null,
                  // "statedesc1": null,
                  // "zipcodedesc1": null,
                  // "latitude": null,
                  // "longitude": null,
                  // "countrydesc1": null
          },
          token: '',
        }
    },
    created: function() {
        //this.search()
        this.fetch()
    },

    methods: {
        search(srhtext='') {
            // alert('Home : ' + srhtext)
            //router.push({ name: 'vendorsearch', params: { srhtext: 'srhtext' }})

            router.push({ path: `/SearchResults` }) // -> /user/123
            //router.push({ path: '/vendorsearch/'+srhtext }) // -> /user/123

        },
        fetch(){

          if (this.loading)
            return;
          // this.ZNavKey = this.$route.params.ZNavKey
          //Get token.
          var config = {
            headers: {
              'Content-Type': "application/x-www-form-urlencoded"
            }
          };

          this.loading = true

          //get Access token by username and password.
          var data_array = {
            grant_type: 'password',
            username: 'saritasa@fooduniverse.com',
            password: 'Apple2018!'
          };

          const data_string = Object.keys(data_array).map((key) => {
            return encodeURIComponent(key) + '=' + encodeURIComponent(data_array[key]);
          }).join('&');

          axios({
            method: 'post',
            url: 'http://ab.fooduniverse.com/service/token',
            data: data_string,
            config
          }).then(response => {

            // Set token by username and password.
              this.token = response.data.access_token
              if (this.token == '')
                  return

              config = {
                headers: {
                  'Content-Type': "application/json",
                  'Authorization': "bearer" + this.token
                }
              }

              // axios.post(this.endpoint + '/AccountPage-User-Address', {
              //     ZNavKey: this.ZNavKey,
              //     config
              // }).then((response) => {
              //     console.log('dietary', response);
              //     this.dietarysettings = response.data.Items[0]?response.data.Items[0]:this.dietarysettings
              // })
          })
        }
    }
}
</script>

